<template>
  <div class="kios-item">
    <content-placeholders class="mt-3" v-if="loading">
      <content-placeholders-text :lines="10" />
    </content-placeholders>
    <div v-else>
      <!-- <v-expansion-panel class="expansion-pl">
        <v-expansion-panel-content value="1">
          <div slot="header">
            <div class="background-triangle-small"> 
              <v-icon size="18" color="white">star_rate</v-icon> 
            </div>Thông tin chung hồ sơ
          </div>
          <v-card style="border-top: 1px solid #ddd;">
            <v-card-text class="py-0">
              
            </v-card-text>
          </v-card>
        </v-expansion-panel-content>
      </v-expansion-panel> -->
      <v-layout class="wrap">
        <v-flex class="pr-2 pb-2">
          <span class="text-bold">Tên hồ sơ: {{dossierDetail.dossierName}}</span>
        </v-flex>
      </v-layout>
      <div class="mt-2">
        <v-tabs
          v-model="active"
          color="cyan"
          dark
          slider-color="yellow"
        >
          <!-- <v-tab key="1" ripple class="mx-2" @click="loadDossiertemplate"> Thành phần hồ sơ </v-tab> -->
          <v-tab key="1" ripple class="mx-2"> Thông tin chung </v-tab>
          <v-tab key="2" ripple class="mx-2"> Tiến trình thụ lý </v-tab>
          <v-tab key="3" ripple class="mx-2" @click="loadVoting()" v-if="dossierDetail['dossierStatus'] === 'done'"> Đánh giá </v-tab>
          <!-- <v-tab key="4" ripple class="mx-2" @click="loadLogs"> Nhật ký sửa đổi</v-tab> -->
          <v-tab-item key="1" class="wrap-scroll wrap-scroll-dossier">
            <v-card >
              <v-card-text class="px-0 py-0">
                
                <v-layout wrap class="px-2 py-2">
                  <v-flex xs12 sm4 class="pr-3">
                    <div class="xs12 sm12 pb-1">
                      <span class="pr-2">Mã hồ sơ: </span>
                      <span class="pl-0 text-bold ">  {{dossierDetail.dossierNo}} </span>
                    </div>
                    <div class="xs12 sm12 pb-1">
                      <span class="pr-2">Hình thức nộp: </span>
                      <span class="pl-0 text-bold ">  {{dossierDetail.online ? 'Nộp trực tuyến' : 'Nộp trực tiếp'}} </span>
                    </div>
                    <!--  -->
                    <div class="xs12 sm12 pb-1">
                      <span class="pr-2">Đơn vị tiếp nhận: </span>
                      <span class="pl-0 text-bold ">  {{dossierDetail.govAgencyName}} </span>
                    </div>
                    <!--  -->
                    <div class="xs12 sm12 pb-1">
                      <span class="pr-2">Trạng thái: </span>
                      <span class="pl-0 text-bold "> {{dossierDetail.dossierStatusText}} </span>
                    </div>
                  </v-flex>
                  <!--  -->
                  <v-flex xs12 sm4>
                    <div class="xs12 sm12 pb-1">
                      <span class="pr-2">Chủ hồ sơ: </span>
                      <span class="pl-0 text-bold "> {{dossierDetail.applicantName}}</span>
                    </div>
                    <div class="xs12 sm12 pb-1" v-if="dossierDetail.online">
                      <span class="pr-2">Ngày gửi: </span>
                      <span class="pl-0 text-bold" v-if="dossierDetail.online"> {{dossierDetail.submitDate}} </span>
                    </div>
                    <!--  -->
                    <div class="xs12 sm12 pb-1">
                      <span class="pr-2">Ngày tiếp nhận: </span>
                      <span class="pl-0 text-bold "> {{dossierDetail.receiveDate}}</span>
                    </div>
                    <!--  -->
                    <div class="xs12 sm12 pb-1" v-if="dossierDetail.dueDate">
                      <span class="pr-2">Ngày hẹn trả: </span>
                      <span class="pl-0 text-bold "> {{dossierDetail.dueDate}}</span>
                    </div>
                    <div class="xs12 sm12 pb-1" v-if="dossierDetail.finishDate">
                      <span class="pr-2">Ngày hoàn thành: </span>
                      <span class="pl-0 text-bold "> {{dossierDetail.finishDate}}</span>
                    </div>
                  </v-flex>
                  <!--  -->
                  <v-flex xs12 sm4>
                    <div class="xs12 sm12 pb-1" style="color:#0b72ba" v-if="dossierDetail.dossierNote&&dossierDetail.dossierNote!=='null'&&dossierDetail.dossierNote.indexOf('<br/>') < 0">Ghi chú:</div>
                    <div class="xs12 sm12 pb-1 overHidden" v-if="dossierDetail.dossierNote&&dossierDetail.dossierNote!=='null'&&dossierDetail.dossierNote.indexOf('<br/>') < 0">
                      <v-tooltip top>
                        <span slot="activator" class="text-bold ">{{dossierDetail.dossierNote}} </span>
                        <span class="pl-0" v-html="dossierDetail.dossierNote"> </span>
                      </v-tooltip>
                    </div>
                    <div class="xs12 sm12 pb-1" v-if="dossierDetail.extendDate">
                      <span class="pl-0 text-bold">Hẹn lại: {{dossierDetail.extendDate}}</span>
                    </div>
                    <!--  -->
                  </v-flex>
                </v-layout>
              </v-card-text>
            </v-card>
          </v-tab-item>
          <v-tab-item key="2" class="wrap-scroll wrap-scroll-dossier">
            <v-card>
              <v-card-text class="px-0 py-0">
                <div>
                  <v-data-table :headers="headers" :items="dossierActions" class="table-landing table-bordered"
                  hide-actions no-data-text="Không có dữ liệu"
                  >
                    <template slot="headerCell" slot-scope="props">
                      <v-tooltip bottom>
                        <span slot="activator">
                          {{ props.header.text }}
                        </span>
                        <span>
                          {{ props.header.text }}
                        </span>
                      </v-tooltip>
                    </template>
                    <template slot="items" slot-scope="props">
                      <td class="text-xs-center">{{props.index + 1}}</td>
                      <td class="text-xs-left">{{props.item.sequenceRole}}</td>
                      <td class="text-xs-left">{{props.item.sequenceName}}</td>
                      <td class="text-xs-left">{{props.item.durationCount}} ngày</td>
                      <td class="text-xs-left">{{props.item.startDate|dateTimeView}}</td>
                      <td class="text-xs-left" v-if="!configDongThap">
                        <div v-for="itemUser in props.item.assignUsers" :key="itemUser.userId">
                          {{itemUser.userName}} <br>
                        </div>
                      </td>
                      <td class="text-xs-left" v-if="!configDongThap">
                        <div v-for="(itemAction, index) in props.item.actions" :key="index">
                          {{itemAction.createDate | dateTimeView}} : <span style="color: #0b72ba">{{itemAction.actionName}}</span>
                        </div>
                        <div v-if="props.item.statusText">
                          <span style="color: green">{{props.item.statusText}}</span>
                        </div>
                      </td>
                    </template>
                  </v-data-table>
                </div>
              </v-card-text>
            </v-card>
          </v-tab-item>
          <v-tab-item key="3" class="wrap-scroll wrap-scroll-dossier">
            <v-card>
              <v-card-text class="px-0 py-0">
                <div class="px-2 py-2">
                  <div v-if="votingItems.length > 0" v-for="(item, index) in votingItems" :key="index" :style="daDanhGia ? 'pointer-events: none !important' : ''">
                    <div class="text-bold pl-3">
                      {{index + 1}}.&nbsp; {{ item.subject }}
                    </div>
                    <v-radio-group class="ml-3 pt-2 mt-0" v-model="item.selected" row>
                      <v-radio v-for="(item1, index1) in item.choices" v-bind:key="index1" :label="item1" :value="index1 + 1"></v-radio>
                    </v-radio-group>
                    
                  </div>
                  <div v-if="votingItems.length === 0" class="mx-3">
                    <v-alert outline color="warning" icon="priority_high" :value="true">
                      Không có đánh giá
                    </v-alert>
                  </div>
                  <div class="ml-3" v-if="votingItems.length > 0 && !daDanhGia">
                    <v-btn color="primary"
                      :loading="loadingVoting"
                      :disabled="loadingVoting"
                      @click="submitVotingDossier"
                    >Gửi đánh giá</v-btn>
                  </div>
                </div>
              </v-card-text>
            </v-card>
          </v-tab-item>
          <v-tab-item key="4" class="wrap-scroll wrap-scroll-dossier">
            <v-card>
              <v-card-text class="px-0 py-0">
                <div v-for="(item, index) in listHistoryProcessing" v-bind:key="item.dossierLogId" class="list_history_style">
                  <td class="px-2 pt-2" :class="index % 2 !== 0 ? 'col-tien-trinh-1' : 'col-tien-trinh-2'">{{ index + 1 }}</td>
                  <td class="text-xs-left px-2 py-2">
                    <p class="mb-1"> <span>{{ item.createDate | dateTimeView }}</span> - <b>{{ item.author }}</b> 
                      : <span style="color: #0b72ba">{{ item.payload.stepName }}</span>
                    </p>
                    <p class="mb-1" v-if="item.content !== '' && item.content !== null">Ý kiến: <span style="color: green" v-html="item.content"></span></p>
                  </td>
              </div>
              </v-card-text>
            </v-card>
          </v-tab-item>
        </v-tabs>
      </div>
      <v-btn class="back-btn" @click="goBack" fab color="primary">
        <v-icon dark>arrow_back</v-icon>
      </v-btn>
    </div>
  </div>
</template>
<script>
  import router from '@/router'
  import Vue from 'vue/dist/vue.min.js'
  import toastr from 'toastr'
  export default {
    props: ['index'],
    components: {},
    data: () => ({
      loading: false,
      loadingAction: false,
      dossierDetail: {},
      dossierDetailMc: '',
      listHistoryProcessing: [],
      dossierActions: [],
      tailieuNop: [],
      tailieuKeyQua: [],
      votingItems: [],
      loadingVoting: false,
      headers: [{
        text: '#',
        align: 'center',
        sortable: false
      },
      {
        text: 'Vai trò',
        align: 'center',
        sortable: false,
        class: 'vaitro_column'
      }, {
        text: 'Công việc',
        align: 'center',
        sortable: false,
        class: 'congviec_column'
      }, {
        text: 'Thời hạn quy định',
        align: 'center',
        sortable: false,
        class: 'thoihanquydinh_column'
      }, {
        text: 'Ngày bắt đầu',
        align: 'center',
        sortable: false,
        class: 'ngaybatdau_column'
      }, {
        text: 'Người thực hiện',
        align: 'center',
        sortable: false,
        class: 'nguoithuchien_column'
      }, {
        text: 'Kết quả',
        align: 'center',
        sortable: false,
        class: 'ketqua_column'
      }],
      configDongThap: true,
      isDvc: false,
      daDanhGia: false
    }),
    computed: {},
    created () {
      let vm = this
      try {
        vm.isDvc = isDvcConfig
      } catch (error) {
      }
      vm.$nextTick(function () {
        try {
          vm.configDongThap = configDongThap
        } catch (error) {
        }
        if (vm.configDongThap) {
          vm.headers = vm.headers.slice(0, 5)
        }
        vm.dossierDetail = this.$store.getters.getDetailDossier
        if (vm.isDvc) {
          if (vm.dossierDetail.submissionNote) {
            let submissionNote = vm.dossierDetail.submissionNote ? JSON.parse(vm.dossierDetail.submissionNote) : ''
            let resultTemp = submissionNote ? submissionNote.data : ''
            if (resultTemp) {
              for (var i = 0; i < resultTemp.length; i++) {
                if (resultTemp[i].hasOwnProperty('actions') && resultTemp[i]['actions'] !== null && resultTemp[i]['actions'] !== undefined) {
                  if (!Array.isArray(resultTemp[i]['actions'])) {
                    let arrActionsTemp = []
                    arrActionsTemp.push(resultTemp[i]['actions'])
                    resultTemp[i]['actions'] = arrActionsTemp
                  }
                }
              }
              vm.dossierActions = resultTemp
            }
          }
        } else {
          vm.loadDossierActionsPublicDvc()
        }
        
        if (vm.dossierDetail.referenceUid) {
          let filter2 = {
            dossierId: vm.dossierDetail.dossierId,
            referenceUid: vm.dossierDetail.referenceUid,
            isDvc: vm.isDvc,
            serverCode: 'SERVER_' + vm.dossierDetail['govAgencyCode']
          }
          vm.$store.dispatch('getDossierDetailAllCase', filter2).then(function (res) {
            vm.dossierDetailMc = res
          }).catch(function (reject) {
          })
        }
      })
    },
    watch: {},
    methods: {
      loadDossierActions () {
        var vm = this
        let dataParams = {
          dossierId: vm.dossierDetail.dossierId
        }
        if (vm.dossierDetail.originality === 0 || vm.dossierDetail.originality === '0') {
        } else {
          vm.$store.dispatch('loadDossierActions', dataParams).then(resultActions => {
            if (resultActions.data) {
              let resultTemp = resultActions.data
              for (var i = 0; i < resultTemp.length; i++) {
                if (resultTemp[i].hasOwnProperty('actions') && resultTemp[i]['actions'] !== null && resultTemp[i]['actions'] !== undefined) {
                  if (!Array.isArray(resultTemp[i]['actions'])) {
                    let arrActionsTemp = []
                    arrActionsTemp.push(resultTemp[i]['actions'])
                    resultTemp[i]['actions'] = arrActionsTemp
                  }
                }
              }
              vm.dossierActions = resultTemp
            }
          })
        }
      },
      loadDossierActionsPublicDvc () {
        var vm = this
        let filter = {
          dossierNo: vm.dossierDetail.dossierNo,
          serverCode: 'SERVER_DVC'
        }
        vm.$store.dispatch('loadingDataHoSoFromMcToDvc', filter).then(resultActions => {
          if (resultActions.submissionNote) {
            let submissionNote = resultActions.submissionNote ? JSON.parse(resultActions.submissionNote) : ''
            let resultTemp = submissionNote ? submissionNote.data : ''
            if (resultTemp) {
              for (var i = 0; i < resultTemp.length; i++) {
                if (resultTemp[i].hasOwnProperty('actions') && resultTemp[i]['actions'] !== null && resultTemp[i]['actions'] !== undefined) {
                  if (!Array.isArray(resultTemp[i]['actions'])) {
                    let arrActionsTemp = []
                    arrActionsTemp.push(resultTemp[i]['actions'])
                    resultTemp[i]['actions'] = arrActionsTemp
                  }
                }
              }
              vm.dossierActions = resultTemp
            }
          }
        })
        
      },
      loadLogs () {
        var vm = this
        let filter = {
          dossierId: vm.dossierDetail.dossierId
        }
        vm.$store.dispatch('getListHistoryProcessingItems', filter).then(function (result) {
          vm.listHistoryProcessing = []
          vm.listHistoryProcessing = result
        })
      },
      loadDossiertemplate () {
        var vm = this
        let filter = {
          dossierTemplateNo: vm.dossierDetail.dossierTemplateNo
        }
        if (vm.dossierDetail.dossierTemplateNo) {
          vm.$store.dispatch('loadDossierTemplates', filter).then(function (result) {
            console.log(result)
            vm.tailieuNop = result.filter(function (item) {
              return item.partType === 1
            })
            vm.tailieuKeyQua = result.filter(function (item) {
              return item.partType === 2
            })
          })
        }
      },
      loadVoting () {
        let vm = this
        if (!vm.isDvc) {
          vm.dossierDetailMc = vm.dossierDetail
        }
        let filter = {
          className: 'dossier',
          classPk: vm.dossierDetailMc.dossierId,
          serverCode: 'SERVER_' + vm.dossierDetailMc['govAgencyCode'],
          isDvc: vm.isDvc
        }
        vm.$store.dispatch('loadVoting', filter).then(function (result) {
          let valid = result.filter(function (item) {
            return item.answersCount
          })
          if (valid && valid.length > 0) {
            vm.daDanhGia = true
            toastr.success('Thông tin hồ sơ trên bạn đã thực hiện đánh giá')
          }
          // 
          for (let index in result) {
            let selectedAns = 0
            for (let index2 in result[index]['answers']) {
              if (result[index]['answers'][index2] !== 0) {
                selectedAns = Number(index2) + 1
                result[index].selected = selectedAns
                break
              }
            }
          }
          vm.votingItems = result
        }).catch(function (reject) {
        })
      },
      submitVoting () {
        let vm = this
        let arrAction = []
        if (vm.votingItems.length > 0) {
          vm.loadingVoting = true
          for (var index in vm.votingItems) {
            vm.votingItems[index]['className'] = 'dossier'
            vm.votingItems[index]['classPk'] = vm.dossierDetail.dossierId
            arrAction.push(vm.$store.dispatch('submitVoting', vm.votingItems[index]))
          }
          Promise.all(arrAction).then(results => {
            vm.loadingVoting = false
            vm.loadVoting()
          }).catch(xhr => {
            vm.loadingVoting = false
          })
        }
      },
      submitVotingDossier () {
        let vm = this
        if (!vm.isDvc) {
          vm.doResultVotingDossier()
        } else {
          vm.doResultVotingMC()
        }
      },
      doResultVotingDossier () {
        var vm = this
        let arrAction = []
        if (vm.votingItems.length > 0) {
          vm.loadingVoting = true
          for (var index in vm.votingItems) {
            vm.votingItems[index]['className'] = 'dossier'
            vm.votingItems[index]['classPk'] = vm.dossierDetailMc.dossierId
            arrAction.push(vm.$store.dispatch('submitVoting', vm.votingItems[index]))
          }
          Promise.all(arrAction).then(results => {
            vm.loadingVoting = false
            toastr.success('Đánh giá của bạn được gửi thành công')
            vm.loadVoting()
          }).catch(xhr => {
            vm.loadingVoting = false
            toastr.error('Gửi đánh giá không thành công')
          })
        }
      },
      doResultVotingMC () {
        let vm = this
        let arrAction = []
        if (vm.votingItems.length > 0) {
          vm.loadingAction = true
          for (var index in vm.votingItems) {
            vm.votingItems[index]['className'] = 'dossier'
            vm.votingItems[index]['classPk'] = vm.dossierDetailMc.dossierId
            vm.votingItems[index]['serverCode'] = 'SERVER_' + vm.dossierDetailMc['govAgencyCode']
            arrAction.push(vm.$store.dispatch('submitVotingMC', vm.votingItems[index]))
          }
          Promise.all(arrAction).then(results => {
            vm.loadingAction = false
            toastr.clear()
            toastr.success('Đánh giá của bạn được gửi thành công')
            vm.loadVoting()
          }).catch(xhr => {
            vm.loadingAction = false
            toastr.clear()
            toastr.error('Gửi đánh giá không thành công')
          })
        }
      },
      goBack () {
        let vm = this
        let current = vm.$router.history.current
        let newQuery = current.query
        let queryString = '?'
        newQuery['detail'] = ''
        for (let key in newQuery) {
          if (newQuery[key] !== '' && newQuery[key] !== 'undefined' && newQuery[key] !== undefined && newQuery[key] !== null && newQuery[key] !== 'null') {
            queryString += key + '=' + newQuery[key] + '&'
          }
        }
        vm.$router.push({
          path: current.path + queryString
        })
      },
      getColor (level) {
        if (level === 2) {
          return 'green'
        } else if (level === 3) {
          return 'orange'
        } else if (level === 4) {
          return 'red'
        }
      }
    },
    filters: {
      dateTimeView (arg) {
        if (arg) {
          let value = new Date(Number(arg))
          return `${value.getDate().toString().padStart(2, '0')}/${(value.getMonth() + 1).toString().padStart(2, '0')}/${value.getFullYear()} ${value.getHours().toString().padStart(2, '0')}:${value.getMinutes().toString().padStart(2, '0')}`
        } else {
          return ''
        }
      }
    }
  }
</script>



